.hpage {
	&__wrapper {
		@include clr; }
	&__container {
		@include m {
			padding: 0; } }
	&__inner {
		@include clr;
		&--first {
			margin-bottom: 40px;
			@include ld {
				margin-bottom: 30px; }
			@include m {
				margin-bottom: 45px; } }
		&--sec {
			margin-bottom: 70px;
			@include ld {
				margin-bottom: 50px; }
			@include dm {
				margin-bottom: 35px; } } }
	&__desc-block, &__step, &__hchars, &__hpslider, &__imgs {
 }		// overflow: hidden
	//	display: inline-block
	//	vertical-align: top
	&__desc-block, &__step, &__hchars {
		width: 935*100/1840 +%;
		float: left;
		@include m {
			width: 100%;
			float: none; } }
	&__desc-block, &__step, &__hchars {
		padding-right: 75*100/1840 +%;
		@include m {
			padding-right: 0; } }
	&__desc-block {
		@include m {
			padding: 0 12px; } }
	&__hpslider, &__imgs {
		width: 905*100/1840 +%;
		float: right;
		@include m {
			float: none;
			width: 100%; } }
	&__hpslider {}
	&__inner {}
	&__title-main {
		padding-bottom: 10px;
		// border-bottom: 4px solid $blue-d
		margin-bottom: 63px;
		position: relative;
		@include ld {
			margin-bottom: 45px; }
		@include dm {
			margin-bottom: 33px; }
		&:after {
			content: '';
			position: absolute;
			background: $blue-d;
			height: 4px;
			width: 140%;
			left: 0;
			bottom: 0;
			z-index: -1;
			@include ld {
				height: 3px; }
			@include m {
				width: 100%; } } }
	&__title-sec {
		margin-bottom: 30px;
		color: $blue-d;
		font-size: 36px;
		font-weight: 700;
		line-height: 1;
		@include ld {
			margin-bottom: 15px;
			font-size: 24px; }
		@include dm {
			font-size: 22px;
			margin-bottom: 10px; } }
	&__equipment {
		margin-bottom: 100px;
		@include ld {
			margin-bottom: 70px; }
		@include dm {
			margin-bottom: 50px; }
		@include m {
			padding: 0 12px;
			margin-bottom: 38px; } }
	&__btns {
		margin-bottom: 125px;
		font-size: 0;
		text-align: center;
		@include ld {
			margin-bottom: 100px; }
		@include dm {
			margin-bottom: 75px; }
		@include m {
			margin-bottom: 40px; } }
	&__btn {
		display: inline-block;
		vertical-align: top;
		text-align: center;
		min-width: 411px;
		border-radius: 10px;
		height: 80px;
		color: $white;
		font-size: 28px;
		font-weight: 700;
		line-height: 1.1;
		padding: 7px 5px;
		background: $blue-d;
		border: 2px solid $blue-d;
		@include trans;
		margin-right: 192*100/1840 +%;
		@include ld {
			min-width: 292px;
			height: 57px;
			font-size: 20px;
			padding: 2px 5px; }
		@include dm {
			font-size: 16px;
			min-width: 240px;
			height: 46px;
			margin-right: 77px; }
		@include m {
			min-width: 240px; }
		@include xs {
			margin-right: 0;
			margin-bottom: 20px;
			display: block;
			width: 240px;
			min-width: auto;
			margin-left: auto;
			margin-right: auto; }
		&:last-of-type {
			margin-right: 0;
			@include xs {
				margin-bottom: 0;
				margin-right: auto; } }
		&:hover {
			color: $blue-d;
			background: $white; } } }

.hdescr {
	padding: 23px 35px 44px 44px;
	box-shadow: $sd;
	position: relative;
	margin-bottom: 50px;
	@include ld {
		padding: 20px 11px 25px 33px;
		margin-bottom: 37px; }
	@include dm {
		padding: 10px 22px 15px 26px;
		margin-bottom: 25px; }
	@include m {
		box-shadow: none;
		padding: 0;
		text-align: right; }
	&:before {
		width: 156px;
		height: 142px;
		content: '';
		position: absolute;
		border-right: 13px solid $orange;
		border-top: 13px solid $orange;
		z-index: 0;
		top: -13px;
		right: -13px;
		@include ld {
			width: 112px;
			height: 101px;
			border-right: 10px solid $orange;
			border-top: 10px solid $orange;
			top: -10px;
			right: -10px; }
		@include dm {
			width: 82px;
			height: 72px;
			border-right: 6px solid $orange;
			border-top: 6px solid $orange;
			top: -6px;
			right: -6px; }
		@include m {
			display: none; } }
	&__more-content {
		display: none; }
	&__article {
		@include m {
			text-align: left; }
		p {
			font-size: 20px;
			color: $black;
			line-height: 1.3;
			@include ld {
				font-size: 16px;
				line-height: 1.4; }
			@include dm {
				font-size: 14px;
				line-height: 1.2; } } }
	&__title {
		text-align: left; }
	&__more {
		display: none;
		@include m {
			display: block;
			margin-top: 10px;
			color: $blue-d;
			text-decoration: underline; } } }

.hpslider {
	background: $white;
	position: relative;
	z-index: 1;

	&__wrapper {
		position: relative;
		padding: 4px 0;
		border-top: 5px solid $blue-d;
		border-bottom: 5px solid $blue-d;
		margin-bottom: 30px;
		@include ld {
			border-top: 4px solid $blue-d;
			border-bottom: 4px solid $blue-d;
			padding: 3px 0;
			margin-bottom: 22px; }
		@include dm {
			border-top: 2px solid $blue-d;
			border-bottom: 2px solid $blue-d;
			padding: 2px 0;
			margin-bottom: 17px; }
		@include m {
			margin-bottom: 0; } }
	&__slide {
		height: 534px;
		@include ld {
			height: 380px; }
		@include dm {
			height: 280px; }
		img {
			object-fit: cover;
			height: 100%;
			width: 100%; }
		@include m {
			display: none; }
		@include xs {
			height: 190px; }
		&.is-visible-mob {
			@include m {
				display: block; } } }
	&__tumb {
		@include m {
			display: none; } }
	&__next, &__prev {
		position: absolute;
		bottom: -26px;
		width: 65px;
		height: 65px;
		text-align: center;
		padding-top: 18px;
		background: $orange;
		cursor: pointer;
		z-index: 1;
		@include trans;
		// border: 2px solid $orange
		@include ld {
			width: 46px;
			height: 46px;
			padding-top: 13px;
			bottom: -20px; }
		@include dm {
			width: 34px;
			height: 34px;
			padding-top: 10px;
			bottom: -14px; }
		svg {
			font-size: 25px;
			fill: $white;
			@include ld {
				font-size: 18px; }
			@include dm {
				font-size: 14px; } }
		&:hover {
			background: $blue-d;
			svg {
				fill: $orange; } } }
	&__prev {
		left: 55px;
		@include ld {
			left: 40px; }
		@include dm {
			left: 27px; } }
	&__next {
		left: 125px;
		background: $blue-d;
		// border-color: $blue-d
		@include ld {
			left: 90px; }
		@include dm {
			left: 65px; }
		&:hover {
			background: $orange;
			svg {
				fill: $blue-d; } } }
	&__tumb {
		font-size: 0;
		text-align: right;
		cursor: pointer; }
	&__tumb-item {
		display: inline-block;
		vertical-align: top;
		margin-left: 15px;
		img {
			max-height: 130px;
			width: auto;
			@include ld {
				max-height: 90px; }
			@include dm {
				max-height: 70px; } } } }
.hstep {
	padding-left: 44px;
	@include ld {
		padding-left: 36px; }
	@include dm {
			padding-right: 0;
			padding-left: 26px; }
	@include m {
		display: none; }
	&--mob {
		@include m {
			display: block;
			padding: 0 12px;
			margin-bottom: 38px; } }
	&__title {
		color: $blue-d;
		font-size: 28px;
		font-weight: 700;
		line-height: 1;
		margin-bottom: 40px;
		position: relative;
		display: inline-block;
		vertical-align: top;
		@include ld {
			font-size: 22px;
			margin-bottom: 30px; }
		@include dm {
			font-size: 20px;
			margin-bottom: 20px; }
		&:after {
			content: '';
			position: absolute;
			width: 226px;
			height: 2px;
			background-color: $blue-d;
			left: 50%;
			transform: translateX(-50%);
			bottom: -20px;
			@include ld {
				width: 161px;
				bottom: -17px; }
			@include dm {
				width: 118px;
				bottom: -9px; } } }
	&__list {
		li {
			margin-bottom: 40px;
			@include ld {
				margin-bottom: 25px; }
			@include dm {
				margin-bottom: 15px;
				font-size: 15px; }
			&:last-of-type {
				margin-bottom: 0; } } } }

.hstep li, .equipment li {
	font-size: 24px;
	line-height: 1.3;
	padding-left: 23px;
	position: relative;
	@include ld {
		font-size: 18px;
		padding-left: 18px; }
	@include dm {
		font-size: 16px;
		padding-left: 13px;
		line-height: 1.1; }


	&:before {
		content: '';
		position: absolute;
		width: 13px;
		height: 13px;
		background: $orange;
		left: 0;
		top: 7px;
		@include ld {
			width: 9px;
			height: 9px;
			top: 5px; }
		@include dm {
			width: 7px;
			height: 7px; } } }
.hchars {
	@include m {
		margin-bottom: 35px; }
	&__title {
		@include m {
			padding: 0 12px; } }
	&__table {
		position: relative;
		border-top: 3px solid $blue-d;
		border-bottom: 3px solid $blue-d; }
	&__line {
		@include clr;
		color: $blue-d;
		font-size: 24px;
		line-height: 1;
		padding: 22px 0;
		@include ld {
			padding: 15px 0px 15px 22px;
			font-size: 18px; }
		@include dm {
			font-size: 16px;
			padding: 10px 0px 10px 18px; }
		@include m;

		&--blue {
			background: #f3f7fb;
			padding: 16px 0;
			@include ld {
				padding: 9px 0px 9px 22px; }
			@include dm {
				padding: 8px 0px 8px 18px; } } }
	&__left {
		float: left; }
	&__right {
		float: right;
		width: 162px;
		@include ld {
			width: 115px; }
		@include m {
			width: 70px; } }
	&__price {
		padding: 34px 34px 34px 0;
		text-align: right;
		color: $blue-d;
		font-size: 30px;
		font-weight: 700;
		line-height: 1;
		@include ld {
			padding: 17px 18px 17px 0;
			font-size: 22px; }
		@include dm {
			font-size: 18px;
			padding: 12px 0px 12px 0; }
		@include m {
			padding: 12px 16px 12px 12px; } } }
.h-imgs {
	&__wrapper {
		display: flex;
		justify-content: space-between;
		@include m {
			padding: 0 12px; }
		@include xs {
			display: block; } }
	&__img {
		width: 292*100/905 +%;
		flex-shrink: 1;
		display: block;
		@include xs {
			width: 100%;
			display: none; }
		@include r(499);
		img {
			object-fit: cover;
			height: 100%;
			width: 100%;
			@include r(499) {
				height: auto; } } }
	&__title {
		padding: 0 12px;
		text-align: center; }
	&__mob-slider {
		position: relative;
		@include m {
			border-bottom: 2px solid $blue-d;
			padding-bottom: 2px; } }
	&__prev, &__next {
		display: none;
		@include xs {
			display: block;
			position: absolute;
			width: 34px;
			height: 34px;
			text-align: center;
			background: $orange;
			bottom: -14px;
			cursor: pointer;
			padding-top: 8px;
			svg {
				fill: $white;
				font-size: 14px; } } }
	&__prev {
		@include xs {
			right: 63px; } }
	&__next {
		@include xs {
			right: 27px;
			background: $blue-d; } }
	&__visible-mob {
		@include xs {
			display: block; } } }
.equipment {
	&__title {
		margin-bottom: 57px;
		@include ld {
			margin-bottom: 40px; }
		@include dm {
			margin-bottom: 30px; }
		@include m {
			margin-bottom: 15px; } }
	&__nav {
		display: none;
		@include m {
			display: block;
			margin-bottom: 20px;
			@include clr;
			a {
				font-size: 14p;
				color: $black;
				line-height: 1;
				text-decoration: underline;
				position: relative;
				&:before {
					content: '';
					position: absolute;
					top: -5px;
					left: -5px;
					bottom: -5px;
					right: -5px; } } } }
	&__nav-left {
		@include m {
			float: left; } }
	&__nav-right {
		@include m {
			float: right; } }
	&__list {
		display: flex;
		flex-shrink: 1;
		justify-content: space-between;
		@include m {
			display: block; } } }
.hequipment {
	display: inline-block;
	vertical-align: top;
	width: 584*100/1840 +%;
	box-shadow: $sd;
	padding-bottom: 220px;
	background: $white;
	position: relative;
	@include ld {
		padding-bottom: 165px; }
	@include m {
		width: 296px;
		margin-left: auto;
		margin-right: auto;
		display: none;
		padding-bottom: 22px; }
	&.is-visible-mob {
		@include m {
			display: block; } }
	&--corner-lt, &--corner-lb {
		&:before {
			content: '';
			position: absolute;
			width: 156px;
			height: 142px;
			border: 12px solid $orange;
			z-index: -1;
			left: -12px;
			@include ld {
				width: 110px;
				height: 101px;
				border: 9px solid $orange;
				left: -9px; }
			@include dm {
				width: 85px;
				height: 75px;
				border: 6px solid $orange;
				left: -6px; } } }
	&--corner-lt {
		&:before {
			top: -12px;
			@include ld {
				top: -9px; }
			@include dm {
				top: -6px; } } }
	&--corner-lb {
		&:before {
			bottom: -12px;
			@include ld {
				bottom: -9px; }
			@include dm {
				bottom: -6px; } } }
	&__inner {
		position: relative;
		z-index: 1; }
	&__title {
		padding: 22px 0 26px;
		background: $blue-d;
		color: $white;
		font-size: 36px;
		font-weight: 700;
		line-height: 1;
		text-align: center;
		margin-bottom: 40px;
		@include ld {
			font-size: 24px;
			padding: 16px 0 20px;
			margin-bottom: 27px; }
		@include dm {
			font-size: 20px;
			padding: 10px 0 14px;
			margin-bottom: 18px; } }
	&__list {
		padding-left: 40px;
		padding-right: 50px;
		@include ld {
			padding-right: 30px;
			padding-left: 28px; }
		@include dm {
			padding-left: 20px; }
		li {
			margin-bottom: 15px;
			@include ld {
				margin-bottom: 10px; }
			&:last-of-type {
				margin-bottom: 0; } } }
	&__bottom {
		position: absolute;
		z-index: 1;
		bottom: 0;
		left: 0;
		width: 100%;
		@include m {
			position: relative;
			margin-top: 15px; } }
	&__price {
		color: $blue-d;
		font-size: 30px;
		line-height: 1;
		font-weight: 700;
		text-align: center;
		margin-bottom: 30px;
		@include ld {
			font-size: 22px;
			margin-bottom: 25px; }
		@include dm {
			font-size: 18px;
			margin-bottom: 18px; } }
	&__quest {
		display: inline-block;
		vertical-align: top;
		width: 27px;
		height: 27px;
		background-color: $white;
		border: 2px solid $orange;
		position: relative;
		border-radius: 50%;
		font-size: 24px;
		font-weight: 700;
		text-align: center;
		color: $orange;
		cursor: pointer;
		@include ld {
			width: 20px;
			height: 20px;
			font-size: 17px;
			top: 1px; }
		@include dm {
			width: 16px;
			height: 16px;
			font-size: 12px; }
		&:before {
			content: '';
			position: absolute;
			top: -5px;
			left: -5px;
			bottom: -5px;
			right: -5px; } }
	&__btn {
		// width: 411px
		width: 410*100/580 +%;
		height: 80px;
		background-color: $white;
		border: 3px solid red;
		border-radius: 10px;
		display: block;
		font-size: 28px;
		font-weight: 700;
		color: $orange;
		text-align: center;
		margin: 0 auto 45px;
		line-height: 1.2;
		@include trans;
		@include ld {
			font-size: 20px;
			height: 57px;
			margin: 0 auto 35px;
			min-width: 292px; }
		@include dm {
			height: 50px;
			font-size: 16px;
			margin: 0 auto 20px;
			line-height: 1.3;
			padding-top: 2px;
			min-width: 258px; }
		@include m {
			margin-bottom: 0;
			width: 258px;
			min-width: auto; }
		&:hover {
			background: $red;
			color: $white; } }
	&__hint {
		left: 50%;
		bottom: calc(50% + 45px);
		@include m {
			left: auto;
			right: 50%; } } }
.hint {
	position: absolute;
	z-index: 3;
	box-shadow: $sd;
	padding: 15px 26px;
	background: $white;
	min-width: 400px;
	text-align: left;
	font-size: 20px;
	line-height: 1.2;
	color: $black;
	font-weight: 400;
	display: none;
	cursor: default;
	@include ld {
		font-size: 16px;
		min-width: 283px;
		padding: 10px 15px 13px; }
	@include dm {
		min-width: 208px;
		padding: 10px 11px 10px 14px;
		font-size: 12px; }

	&.is-open {
		display: block; }
	&__b-line {
		margin-top: 20px;
		@include dm {
			margin-top: 10px; } }
	a {
		text-decoration: underline;
		color: $blue-d;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: -5px;
			left: -5px;
			right: -5px;
			bottom: -5px; }
		&:hover {
			text-decoration: none; } }
	&:after {
		content: '';
		position: absolute;
		bottom: -25px;
		left: 0;
		@include arr(35, 25, $white, tl);
		@include m {
			@include arr(35, 25, $white, tr);
			right: 0;
			left: auto; } } }
