.cpage {
	.banner {
		background-size: contain;
		background-position: 50% 0;
		background-repeat: no-repeat;
		height: 718px;
		@include ld {
			height: 496px; }
		@include dm {
			height: 365px; }
		@include m {
			display: none; } } }
.ctopline-filter {
	margin-top: 4px;
	float: right;
	font-size: 0;
	@include m {
		display: none; }

	a, span {
		display: inline-block;
		vertical-align: top;
		font-size: 20px;
		color: black;
		padding-bottom: 3px;
		@include ld {
			font-size: 16px; } }
	a {
		border-bottom: 1px dashed transparent;
		margin-left: 44px;
		@include ld {
			margin-left: 37px; }
		@include dm {
			margin-left: 33px; }
		@include trans;
		&:hover {
			color: $orange; } }
	.is-active {
		color: $orange;
		border-bottom: 1px dashed $orange; } }
.catalog {
	&__topline {
		border-bottom: 4px solid $blue-d;
		padding-bottom: 11px;
		margin-bottom: 66px;
		@include clr;
		@include ld {
			border-bottom: 3px solid $blue-d;
			padding-bottom: 9px;
			margin-bottom: 60px; }
		@include dm {
			padding-bottom: 5px;
			margin-bottom: 35px; }
		@include m {
			margin-bottom: 15px; } }

	&__title {
		float: left; }
	&__wrapper {
		@include clr; }
	&__left {
		width: 455px;
		float: left;

		position: relative;
		&:before {
			content: '';
			position: absolute;
			width: 156px;
			height: 142px;
			border-top: 13px solid $orange;
			border-left: 13px solid $orange;
			left: -13px;
			top: -13px;
			z-index: 0;
			@include ld {
				width: 117px;
				height: 107px;
				border-top: 8px solid $orange;
				border-left: 8px solid $orange;
				left: -8px;
				top: -8px; }
			@include dm {
				width: 86px;
				height: 78px; }
			@include m {
				display: none; } }
		@include ld {
			width: 312px; }
		@include dm {
			width: 223px; }
		@include m {
			float: none;
			width: 60%;
			margin: 0 auto;
			@include clr; }
		@include xs {
			width: 100%; } }

	&__list {
		margin-left: 505px;
		width: calc(100% - 505px);
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		@include ld {
			margin-left: 332px;
			width: calc(100% - 332px); }
		@include dm {
			margin-left: 243px;
			width: calc(100% - 243px); }
		@include m {
			display: block;
			width: 100%;
			margin-left: 0; } }
	&__spec-link {
		border: 2px solid $orange;
		height: 80px;
		display: block;
		color: $orange;
		font-size: 24px;
		text-align: center;
		line-height: 80px;
		margin-bottom: 39px;
		@include lp {
			font-size: 22px; }
		@include ld {
			height: 61px;
			line-height: 61px;
			font-size: 18px;
			margin-bottom: 29px; }
		@include dm {
			height: 45px;
			line-height: 45px;
			font-size: 15px;
			margin-bottom: 20px; }
		@include r(1020) {
			font-size: 13px; } }
	&__ask-block {
		box-shadow: $sd;
		padding: 30px 5px 38px;
		width: 100%;
		position: absolute;
		bottom: 0;
		width: 100%;
		left: 0;
		@include ld {
			padding: 22px 5px 28px; }
		@include dm {
			padding: 10px 5px 17px; }
		svg {
			fill: $blue-d;
			right: 7%;
			top: -21px;
			@include lp {
				right: 4%;
				top: -37px; }
			@include ld {
				font-size: 75px;
				right: 9%;
				top: -21px; }
			@include lg {
				top: -24px;
				font-size: 60px; }
			@include dm {
				top: -36px; }
			@include r(1170) {
				font-size: 45px;
				top: -18px; }
			@include r(1150) {
				font-size: 35px;
				top: -9px; } }
		.ask-block__sec {
			font-size: 28px;
			@include ld {
				font-size: 22px;
				margin-bottom: 30px; }
			@include dm {
				font-size: 20px;
				margin-bottom: 15px; } }
		.catalog__spec-text {
			font-size: 20px;
			color: $black;
			line-height: 1.5;
			margin-bottom: 58px;
			position: relative;
			@include r(1800) {
				font-size: 16px; }
			@include ld {
				margin-bottom: 45px; }
			@include dm {
				margin-bottom: 20px; }
			&:after {
				content: '';
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				width: 226*100/420 +%;
				height: 2px;
				background: $blue-d;
				bottom: -29px;
				@include ld {
					bottom: -24px; }
				@include dm {
					bottom: -9px; } } }
		.ask-block__top-text {
			margin-bottom: 20px;
			@include ld {
				font-size: 22px;
				margin-bottom: 12px; }
			@include dm {
				font-size: 18px;
				margin-bottom: 10px; }
			&:after {
				display: none; } }
		.ask-block__b-text {
			margin-bottom: 25px;
			@include ld {
				font-size: 24px;
				margin-bottom: 12px; }
			@include dm {
				font-size: 20px;
				margin-bottom: 10px; } }
		.ask-block__btn {
			@include ld {
				height: 45px;
				min-width: 219px;
				span {
					line-height: 45px;
					font-size: 16px; } }
			@include dm {
				height: 33px;
				min-width: auto;
				span {
					line-height: 33px; } } } }
	&__item--spec-mob {
		display: none;
		@include m {
			display: block;
			width: 296px;
			margin: 0 auto 35px; }

		.ask-block {
			@include m {
				display: block;
				width: 100%;
				position: relative;
				left: 0;
				bottom: 0; }

			&__btn {
				@include m {
					max-width: 100%; } }
			svg {
				font-size: 52px;
				top: -17px; } }
 } }		// &--spec
.prod-item, .catalog__item--spec {
	width: 420*100/1334 +%;
	position: relative;
	// overflow: hidden
	margin-bottom: 25px;
	@include ld {
		margin-bottom: 18px; } }
.catalog__item--spec {
	@include m {
		display: none; } }
.prod-item {
	border: 2px solid #e7e5e5;
	@include trans;
	padding: 13px 13px 28px;
	@include ld {
		padding: 9px 9px 20px; }
	@include dm {
		padding: 6px 6px 13px; }
	@include m {
		display: block;
		width: 296px;
		margin: 0 auto 18px; }
	&:hover {
		border-color: $orange; }
	&__img {
		height: 228px;
		margin-bottom: 15px;
		@include ld {
			height: 169px;
			margin-bottom: 12px; }
		@include dm {
			height: 124px;
			margin-bottom: 10px; }
		img {
			object-fit: cover;
			height: 100%;
			width: 100%; } }
	&__title {
		color: $blue-d;
		font-size: 28px;
		font-weight: 700;
		line-height: 1;
		display: block;
		text-align: center;
		margin-bottom: 30px;
		position: relative;
		&:after {
			content: '';
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			width: 226*100/420 +%;
			height: 2px;
			background: $blue-d;
			bottom: -13px;
			@include ld {
				bottom: -11px; }
			@include dm {
				bottom: -9px; } }
		@include ld {
			font-size: 20px;
			margin-bottom: 22px; }
		@include dm {
			font-size: 18px;
			margin-bottom: 20px; } }
	&__line {
		margin-bottom: 20px;
		@include clr;
		@include ld {
			margin-bottom: 15px; }
		@include dm {
			margin-bottom: 10px; }
		span {
			color: $black;
			font-size: 24px;
			line-height: 1;
			@include ld {
				font-size: 18px; }
			@include dm {
				font-size: 16px; } } }
	&__char-name {
		float: left; }
	&__char-value {
		float: right; }
	&__price {
		display: block;
		margin-top: 40px;
		font-size: 30px;
		font-weight: 700;
		color: $blue-d;
		text-align: center;
		margin-bottom: 29px;
		@include ld {
			margin-top: 28px;
			font-size: 22px;
			margin-bottom: 20px; }
		@include dm {
			margin-top: 25px;
			font-size: 18px;
			margin-bottom: 15px; } }
	&__btn {
		width: 258px;
		height: 50px;
		background: $white;
		border: 2px solid $red;
		border-radius: 3px;
		line-height: 50px;
		color: $orange;
		font-size: 28px;
		font-weight: 700;
		line-height: 50px;
		text-align: center;
		display: block;
		margin: 0 auto;
		@include trans;
		&:hover {
			color: $white;
			background: $red; }
		@include ld {
			width: 192px;
			height: 37px;
			font-size: 20px;
			line-height: 34px; }
		@include dm {
			width: 141px;
			height: 28px;
			font-size: 16px;
			line-height: 28px; } } }
.fslider {
	&--price {
		@include dm {
			.fslider__left, .fslider__right {
				float: none;
				input {
					width: 80px; } }
			.fslider__left {
				margin-bottom: 10px; }
			.filter__spec-span {
				display: inline-block;
				vertical-align: top;
				padding-top: 5px;
				width: 20px; } } }
	&__path {
		height: 3px;
		background: #e7e5e5;
		position: relative;
		margin-bottom: 17px;
		@include ld {
			height: 2px;
			margin-bottom: 12px; } }
	&__min, &__max, &__avr {
		position: absolute;
		top: 50%;
		background-color: $orange;
		cursor: pointer;
		&:before {
			content: '';
			position: absolute;
			top: -5px;
			left: -5px;
			bottom: -5px;
			right: -5px; } }
	&__min, &__max {
		width: 17px;
		height: 17px;
		border-radius: 50%;
		transform: translateY(-50%);
		@include ld {
			width: 13px;
			height: 13px; } }
	&__min {
		left: 0; }
	&__max {
		right: 0;
		background: $blue-d; }
	&__avr {
		width: 14px;
		height: 14px;
		margin-left: -7px;
		margin-top: -7px;
		transform: rotate(45deg);
		left: 39%;
		z-index: 1;
		@include ld {
			width: 12px;
			height: 12px;
			margin-left: -6px;
			margin-top: -6px; } }
	&__path-select {
		width: 40%;
		background: $orange;
		height: 100%; }
	&__text {
		margin-bottom: 30px;
		@include ld {
			margin-bottom: 24px; }
		@include dm {
			margin-bottom: 14px; } }
	&__inputs {
		margin-bottom: 40px;
		@include ld {
			margin-bottom: 32px; }
		@include dm {
			margin-bottom: 20px; } }
	&__text, &__inputs {
		@include clr;
		span {
			position: relative; }
		span, input {
			font-size: 20px;
			line-height: 1;
			color: $black;
			@include ld {
				font-size: 16px; } }
		input {
			width: 112px;
			height: 39px;
			background-color: $white;
			border: 2px solid #e7e5e5;
			line-height: 39px;
			padding: 0 6px;
			@include ld {
				width: 83px;
				height: 29px; }
			@include dm {
				width: 55px;
				height: 27px;
				line-height: 27px; } } }
	sub {
		position: relative;
		top: -6px;
		font-size: 14px;
		@include ld {
			top: -6px;
			font-size: 12px; } }
	&__text-min, &__left {
		float: left; }
	&__text-max, &__right {
		float: right; } }

.filter {
	position: relative;
	padding: 28px 39px 23px 17px;
	z-index: 1;
	margin-bottom: 38px;
	box-shadow: $sd;
	@include ld {
		padding: 22px 17px 13px;
		margin-bottom: 30px; }
	@include dm {
		padding: 13px 13px 16px; }
	@include m {
		box-shadow: none;
		margin-bottom: 0;
		@include clr;
		padding: 0; }
	&__wrapper {
		@include m {
			width: 240px;
			position: absolute;
			left: 2px;
			background: $white;
			padding: 14px 20px 20px 9px;
			top: 130%;
			box-shadow: $sd; } }
	&__title {
		display: block;
		color: $blue-d;
		font-size: 30px;
		font-weight: 700;
		line-height: 1;
		margin-bottom: 35px;
		padding-left: 26px;
		@include ld {
			margin-bottom: 30px;
			font-size: 22px; }
		@include dm {
			font-size: 18px;
			margin-bottom: 20px; }
		&--spec {
			margin-bottom: 18px;
			@include ld {
				margin-bottom: 13px; }
			@include dm {
				margin-bottom: 12px; } } }

	&__mob-btn {
		display: none;
		@include m {
			display: block;
			width: 144px;
			height: 35px;
			background-color: $white;
			border: 2px solid $orange;
			color: $orange;
			font-size: 18px;
			line-height: 35px;
			text-align: center;
			float: left; } } }

.fbuttons {
	&__title {
		margin-bottom: 25px;
		@include dm {
			margin-bottom: 12px; } }
	&__list {
		font-size: 0;
		margin-bottom: 45px;
		@include ld {
			margin-bottom: 34px; }
		@include dm {
			margin-bottom: 20px; } }
	&__btn {
		width: 63px;
		height: 63px;
		text-align: center;
		border: 2px solid #e7e5e5;
		color: $black;
		font-size: 30px;
		font-weight: 400;
		line-height: 1;
		padding-top: 5px;
		display: inline-block;
		vertical-align: top;
		background: $white;
		margin-right: 19px;
		@include trans;
		@include ld {
			width: 47px;
			height: 47px;
			margin-right: 14px;
			font-size: 22px; }
		@include dm {
			width: 35px;
			height: 35px;
			font-size: 16px;
			margin-right: 11px; }
		&.is-active {
			background: $orange;
			color: $white; }
		&:hover {
			background: $orange;
			color: $white; }
		&:last-of-type {
			margin-right: 0; } } }
.fcheck {
	input {
		display: none;
		&:checked + label {
			.fcheck__icon {
				&:before {
					display: block; } } } }
	&__line {
		font-size: 0;
		padding-left: 31px;
		position: relative;
		margin-bottom: 15px;
		&:last-of-type {
			margin-bottom: 0; }
		@include ld {
			padding-left: 22px; } }
	&__icon {
		width: 21px;
		height: 21px;
		background-color: white;
		border: 2px solid #e7e5e5;
		position: absolute;
		display: inline-block;
		margin-right: 10px;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		@include ld {
			width: 16px;
			height: 16px; }
		&:before {
			content: '';
			@include vcenter;
			@include s(check-png);
			top: 20%;
			display: none;
			@include ld {
				@include s(check-png2); } } }
	span {
		font-size: 20px;
		line-height: 1;
		color: $black;
		@include ld {
			font-size: 16px; } } }
.fsort {
	display: none;
	@include m {
		display: block;
		float: right;
		width: 130px;
		position: relative;
		&>span {
			display: block;
			border-bottom: 1px solid $blue-d;
			position: relative;
			span {
				width: 16px;
				height: 16px;
				display: block;
				position: absolute;
				right: 0;
				bottom: 0px;
				background: $blue-d;
				&:after {
					content: '';
					@include vcenter;
					@include arr(9, 7, $white, b); } } }
		&__dropdown {
			position: absolute;
			z-index: 3;
			width: calc(100% + 12px);
			background: $white;
			top: 100%;
			right: 0;
			padding-top: 5;
			padding-left: 12px;
			padding-bottom: 12px;
			display: none;
			&.is-open {
				display: block; }
			a {
				display: block;
				color: $black;
				padding: 2px 0; } }
		&__view {
			span {
				color: $orange; } } } }
.cnav {
	padding-top: 40px;
	padding-bottom: 112px;
	text-align: center;
	@include ld {
		padding-top: 27px;
		padding-bottom: 68px; }
	@include dm {
		padding-top: 23px;
		padding-bottom: 45px; }
	@include m {
		padding-top: 10px;
		padding-bottom: 36px; }

	&__inner {
		font-size: 0;
		display: inline-block;
		vertical-align: top;
		position: relative;
		right: -14%;
		@include ld {
			right: -13%; }
		@include m {
			right: 0; } }

	&__link {
		display: inline-block;
		vertical-align: top;
		width: 34px;
		height: 34px;
		border: 2px solid transparent;
		font-size: 20px;
		text-align: center;
		line-height: 34px;
		color: #797979;
		@include ld {
			width: 26px;
			height: 26px;
			font-size: 16px;
			line-height: 26px; }
		svg {
			fill: #797979;
			font-size: 16px;
			@include ld {
				font-size: 13px; } }
		&.is-active {
			border-color: $orange;
			color: $orange; } } }
